@import url('https://fonts.googleapis.com/css2?family=Inter:ital,opsz,wght@0,14..32,100..900;1,14..32,100..900&display=swap');

@font-face {
    font-family: TavernS;
    src: url(../fonts/tavern-s-bold.otf);
}

body {
    text-wrap: pretty;
    word-wrap: break-word;
    font-family: "Inter", sans-serif;
    margin: 0;
    background-color: #fff6ec;
}

#logo {
    width: 252px;
    height: auto;
}

header {
    position: sticky;
    z-index: 2;
    top: 0;
    padding: 20px 40px;
    transition: height 0.3s ease-in-out, padding 0.3s ease;
    background-color: #bf7a49b9;
    background: linear-gradient( #bf7a49b9, #bf7a49b9 ), url(../images/background.jpeg) center no-repeat;
    background-size: cover;
}

.shrink {
    padding: 5px 40px;
}

main, footer {
    padding: 0px 40px;
}

.desktop {
    margin-top: 10px;
    font-size: 20px;
    display: flex;
    gap: 30px;
    justify-content: flex-end;
}

header a {
    color: white;
    text-decoration: none;
    font-weight: 500;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    transition: all 0.25s ease-in;
}

main a {
    color: #2c5aff;
}

main a:hover {
    text-decoration: none;
    color: #6e88e9;
}

header a:hover {
    opacity: 0.7;
    scale: 1.05;
}

.mlinks a:hover {
    scale: 1;
}

header a:active {
    opacity: 1.0;
    text-decoration: underline;
}

header h1 {
    margin-top: 0;
}

main {
    position: relative;
}

#main {
    margin: 0 -40px;
    width: 100vw;
    height: calc(100vh - 119px);
    object-fit: cover;
    object-position: center top;
}

#imagetext {
    position: absolute;
    top: 0;
    left: 0;
    padding: 20px 40px;
    width: calc(100vw - 80px);
    background-color: #EDFDF549;
    backdrop-filter: blur(15px);
    margin: 0;
}

footer {
    margin-top: 40px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.prev_button, .next_button {
    color: white;
    text-decoration: none;
    padding: 10px 20px;
    margin-bottom: 20px;
    border-radius: 8px;
    font-weight: 700;
    font-size: 18px;
    transition: all 0.2s ease-in;
}

.prev_button {
    color: white;
    background: #9BA4C6;
}

.next_button {
    color: white;
    background: #618A56;
}

.prev_button:hover, .next_button:hover {
    color: white;
    opacity: 0.8;
    box-shadow: -2px -2px rgba(0, 0, 0, 0.404) inset;
}

.sidebyside {
    display: flex;
    gap: 50px;
}

.sidebyside aside img {
    width: 20vw;
}

.sidebyside aside p {
    text-align: center;
    font-size: 16px;
}

h1, h2 {
    font-family: TavernS;
}

h2 {
    font-size: 26px;
    color: #618A56;
}

p {
    font-size: 18px;
}

header i {
    color: white;
    font-size: 45px;
    cursor: pointer;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}

.mlinks {
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 20px;
    font-size: 22px;
    height: 0;
    overflow: hidden;
    transition: height 0.3s ease-in-out;
}

.showlinks {
    height: calc(100vh - 118px);
}

.shrink .showlinks {
    height: calc(100vh - 88px);
}

.current {
    font-weight: bold;
    text-decoration: underline;
}

.director {
    display: flex;
    align-items: flex-start;
    text-align: left;
    gap: 30px;
}

.actors {
    display: flex;
    gap: 20px;
}

.actor {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.director {
    padding-bottom: 40px;
}

.director h2, .headers h2 {
    color: #844E27;
    font-size: 22px;
}

.director h2:first-child {
    margin-top: 0;
}

main img:not(#main) {
    border: 4px #618A56 solid;
    border-radius: 4px;
}

.director img {
    box-sizing: border-box;
}

main b {
    font-weight: 500;
}

main #corbucci img {
    border: 4px #9BA4C6 solid;
}

.actor > img {
    width: 100%;
}

.actor p {
    font-weight: bold;
}

.director > img {
    width: 20%;
    height: auto;
}

audio {
    color: #618A56;
}

.sidebyside > aside {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.group > p {
    font-style: italic;
}

nav {
    width: max-content;
    height: 40px;
}

.bar {
    display: flex;
    justify-content: space-between;
    align-items: end;
    transition: align-items 0.3s ease-in-out;
}

@media screen and (min-width: 1300px) {
    .mobile {
        display: none;
    }
}

@media screen and (max-width: 480px) {
    .bar > a, #logo {
        width: 100%;
    }
}

@media screen and (max-width: 1300px) {
    footer {
        flex-direction: column;
    }
    .next_button {
        text-align: right;
    }
    .bar {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
    }
    #close {
        display: none;
    }
    .desktop {
        display: none;
    }
    .sidebyside {
        flex-direction: column;
        gap: 10px;
    }
    .sidebyside aside {
        display: flex;
        flex-direction: column;
        gap: 20px;
        order: -1;
    }
    .sidebyside aside img {
        width: 100%;
    }
    .director {
        display: flex;
        flex-direction: column;
    }
    .director > img {
        width: 100%;
        height: 350px;
        object-fit: cover;
    }
    .actors {
        flex-wrap: wrap;
    }
    .actor {
        flex: calc(50% - 10px);
    }
}

.fa-solid:hover {
    opacity: 0.7;
}